<template>
    <div class="container back-one">
        <div class="card-list" v-if="list && list.result!=108">
            <ul>
                <li v-for="item in list" :key="item.id">
                    <router-link :to="'/cardDetail/' + item.id">
                        <div class="pict">
                            <img :src="require(`@/static/img/bank/${bankPicture(item.bankName)}`)" alt="">
                        </div>
                        <div class="text">
                            <h2>{{item.bankName}}（尾号{{item.bankAccount | lotteryNumber(4)}}  {{item.subBankName}}）</h2>
                            <p>极速到账  *单笔金额:10~10000元</p>
                        </div>
                    </router-link>
                </li>
            </ul>
            <div class="public-btn mt25">
                <router-link class="button" to="/cardAdd">添加银行卡</router-link>
            </div>
        </div>
        <div class="error" v-else-if="list.result==108">{{list.description}}</div>
        <loadding v-else></loadding>
    </div>
</template>

<script>
import {
    getUserBankCardList
} from "@/tool/common/constants.js";
import { mapState } from "vuex";
export default {
    data(){
        return {
            // list: false
        }
    },
    created(){
        this.$store.commit(`User/${getUserBankCardList}`, false)
        // this.getBankCard()
        this.$store.dispatch(`User/${getUserBankCardList}`, this.baseUser)
    },
    computed:{
        ...mapState('User', {
            list: state => state.getUserBankCardList ? state.getUserBankCardList.userBankCardList ? state.getUserBankCardList.userBankCardList : state.getUserBankCardList : state.getUserBankCardList
        })
    },
    methods: {
        getBankCard(){
            this.$api.user.getUserBankCardList(this.baseUser)
            .then(res=>{
                console.log(res);
                this.list = res.userBankCardList
            })
        }
    },
}
</script>

